import React, { useContext, useState } from 'react';
import { Fade } from 'react-reveal';
import emoji from 'react-easy-emoji';
import './Greeting.css';
import landingPerson from '../../assets/lottie/landingPerson';
import DisplayLottie from '../../components/displayLottie/DisplayLottie';
import SocialMedia from '../../components/socialMedia/SocialMedia';
import Button from '../../components/button/Button';

import { illustration, greeting } from '../../portfolio';
import StyleContext from '../../contexts/StyleContext';

export default function Greeting() {
  const { isDark } = useContext(StyleContext);

  const [isHovering, setIsHovering] = useState(false);

  const handleMouseEnter = () => {
    setIsHovering(true);
  };

  const handleMouseLeave = () => {
    setIsHovering(false);
  };

  if (!greeting.displayGreeting) {
    return null;
  }

  return (
    <Fade bottom duration={1000} distance="40px">
      <div className="greet-main" id="greeting">
        <div className="greeting-main">
          <div className="greeting-text-div">
            <div>
              <h1
                className={isDark ? 'dark-mode greeting-text' : 'greeting-text'}
              >
                {' '}
                {greeting.title}{' '}
                <span className="wave-emoji">{emoji('üëã')}</span>
              </h1>
              <p
                className={
                  isDark
                    ? 'dark-mode greeting-text-p'
                    : 'greeting-text-p subTitle'
                }
              >
                {greeting.subTitle}
              </p>
              <SocialMedia />
              <div className="button-greeting-div">
                <Button text="Contact me" href="#contact" />
                <Button
                  text="See my resume"
                  newTab={true}
                  href={greeting.resumeLink}
                />
              </div>
            </div>
          </div>
          <div 
            className="greeting-image-div"
            // this if for center my profile pic
            style={{ textAlign:'center', margin:'auto', display:'block',
            // backgroundColor: isHovering ? 'salmon' : '',
            // color: isHovering ? 'white' : '',
           }}
            // onMouseEnter={handleMouseEnter}
            // onMouseLeave={handleMouseLeave}
          >
            {/* {illustration.animated ? (
              <DisplayLottie animationData={landingPerson} />
            ) : ( */}
              <img
                alt="man sitting on table"
                style={{ borderRadius:'50%', height:'auto', cursor:'pointer',
                // transitionProperty:'width',
                // transitionDuration:'0.35s',
                transition: 'all .5s ease-in-out',
                width: isHovering ? '25rem' : '20rem',
                transform: isHovering ? 'rotate(1080deg)' : ''
              }}
              onMouseEnter={handleMouseEnter}
              onMouseLeave={handleMouseLeave}
                src={require('../../assets/images/profilom_png.png')}
              ></img>
            {/* )} */}
          </div>
        </div>
      </div>
    </Fade>
  );
}
